
.me-post-card-container {
  margin: 0;
  border: 0;
}
.me-post-card {
  margin: 10px 0;
}
.me-post-card .card-content {
  padding-top: 0;
}
.me-text.text {
  word-wrap: break-word;
  text-indent: 2em;
  font-size: 20px;
  padding: 4px;
}
.me-user {
  font-size: 14px;
  margin-right: 100px;
}
.me-user .me-name {
  color: #f7b453;
}
.me-avatar img {
  width: 40px;
  height: 40px;
  border-radius: 4px;
}
.me-image img {
  width: 100%;
  height: 200px;
  padding: 4px;
}
